import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Draw from '@/utils/draw';
import $type from '@/utils/type';

let drawBoard: Draw;

const handleMouseUp = (event: MouseEvent) => {
    const { pageX: x, pageY: y } = event;
    drawBoard.endDraw(x, y);
