    @Prop({ default: '#21242C' })
    defaultColor: string;

    @Prop({ default: false })
    readonly: boolean;

    @Prop({ default: false })
    ifRunClear: boolean;

    @Prop({ default: false })
    isBtnLoading: boolean;

    @Prop({ default: false })
    isBtnDisabled: boolean;

    @Prop({
        default: () => [
            '#01A995',
            '#E84D39',
            '#74CF70',
            '#FFBE26',
            '#CA337C',
            '#7853AB',
            '#11ACCD',
            '#20A0FF',
            '#21242C'
        ]
    })
    penColors: string[];

    // data
    isShowAllTool: boolean = false;
